














import Vue from "vue";
import {Component} from "vue-property-decorator";

@Component
export default class Form extends Vue {
    todoText = "";

    submitText(e: Event): void {
        e.preventDefault();

        if (this.todoText.length) {
            this.$emit("dispatch", "ADD", {
                id: Date.now(),
                text: this.todoText,
                completed: false
            });

            this.todoText = "";
        }
    }
}
